	module.exports = {"data":{"markdownRemark":{"html":"<p>私は午前中に師匠にやって来た。私は江西省の仲間のように上海で働いた。彼らは新しい空間があるように、彼らはアルミニウム合金の窓で家のバルコニーを密封させて、それは心がはるかに幸せだと思われる。</p>\n<p>中国人の心の中には、もう少しスペースがあり、何よりも楽しいものがあります。おそらく中国には人があまりにも多く、スペースは比較的小さいです。それが個人の閉鎖空間であれば、他人と分かち合うことなく個人が共有する空間が少なくなります。したがって、混雑した場所では、人々は自分自身の少しのスペースを得る、稀少なリソースです。自分の宇宙を追求するというこの願いは、社会生活のパターンから、そして社会の発展と人々の霊的な領域の内的変化からも生じます。スペースの狭さだけで、人々は少しスペースを大切にします。あなたがそれを広げることができれば、それはまたうらやましい業績です。さらに、精神的な領域も近代化しており、他者と共有されない領域を必要とする独自のプライバシーを持つことがますます望まれています。しかし、この需要が過度に発達すれば、それは現代西欧社会が直面する問題のように、社会への一種の圧力になるでしょう。私たちはこの段階に達していませんが、すでにそこに行っています。もちろん、まだ遠いです。現代社会におけるこの矛盾を解決する方法はありますか？それは個人の空間を広げるだけでなく、誰とでも緊密な調和のとれた関係を持っています。まずそれを勉強しましょう。</p>\n<p>夕方、私は自営業者に5食と1スープを食べさせ、43元を使いました。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0156.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0156.md","frontmatter":{"title":"10月9日 日曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0156.md absPath of file >>> MarkdownRemark"}}

